<template>
  <div class="login-page">
    <div class="layer bg" id="login"></div>
    <div class="layer flex-center">
      <!--  -->
      <div class="form-group">
        <el-card>
          <el-form ref="loginForm" label-position="top" :rules="rules" :model="formLogin">
            <el-form-item  prop="username">
              <el-input type="text" v-model="formLogin.username" placeholder="">
                <span slot="prepend"></span>
              </el-input>
            </el-form-item>
            <el-form-item prop="password">
              <el-input type="password" v-model="formLogin.password" placeholder="">
                <span slot="prepend"></span>
              </el-input>
            </el-form-item>
            <el-button @click="submit" type="primary" class="button-login"></el-button>
          </el-form>
        </el-card>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
require('particles.js')
// 
// https://vincentgarreau.com/particles.js/#default
import config from './config/default'
import Cookies from 'js-cookie'
import md5 from 'md5'
export default {
  data () {
    return {
      formLogin: {
        username: '',
        password: ''
      },
      rules: {
        username: [
          { required: true, message: '', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  mounted () {
    // 
    particlesJS('login', config)
  },
  methods: {
    // 
    submit () {
      const md5Valid = () => {
        // console.log(md5(this.formLogin.username + this.formLogin.password))
        const val = '52faf512f26f99ec6153582a39773b75'
        return new Promise((resolve, reject) => {
          if (md5(this.formLogin.username + this.formLogin.password) === val) {
            resolve(val)
          } else {
            reject()
          }
        })
      }
      this.$refs.loginForm.validate((valid) => {
        if (valid) {
          md5Valid()
            .then(val => {
              const setting = {
                expires: 1
              }
              Cookies.set('login', val, setting)
              this.$router.push({
                name: 'index'
              })
            })
            .catch(() => {
              this.$message.error('')
            })
        } else {
          return false
        }
      })
    }
  }
}
</script>

<style lang="scss">
@import './style.scss';
</style>




